body, html, #root, .App {
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat';
}
.App:before {
    display: block;
    content: '';
    position: fixed;
    z-index: -10;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: -webkit-gradient(linear, left bottom, right top, from(#fff),to(#e2fdfa));
    background: -webkit-linear-gradient(bottom left, #fff,#e2fdfa);
    background: -o-linear-gradient(bottom left, #fff,#e2fdfa);
    background: linear-gradient(to top right, #fff,#e2fdfa);
}


* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box
}


